 #009900;\n}/*-- .toggle-sidebar i:hover --*/\n.sidebar-menu li{\r\n  min-height: 40px;\r\n  overflow-x: hidden;\r\n  vertical-align: middle;\n}\n.sidebar-menu ul li{\r\n  min-height: 30px;\r\n  overflow-x: hidden;\n}\n.sidebar-menu li, .sidebar-menu ul > li {\r\n  position: relative;\r\n  margin: 0;\r\n  padding: 0;\n}\r\n\r\n/*padding: 13px 2% 13px 5%;*/\n.sidebar-menu  li > a{\r\n  display: block;\r\n  padding: 13px 2% 13px 2%;\n}\r\n\r\n/*padding: 10px 20px 10px 50px;*/\n.sidebar-menu  ul li > a{\r\n  padding: 10px 5% 10px 3%;\r\n  display: block;\n}\n.sidebar-menu li > a:hover,.sidebar-menu ul li > a :hover{\r\n    cursor: pointer;\r\n    background: #eaeaea;\n}\n.appActive, .appSubActive{\r\n  background: #eaeaea;\n}/*-- app-active --*/\n.appActive ul{\r\n  background: #f4f4f4;\n}\r\n\r\n/*---------------------------------------------------------          \r\n\r\n                  RESPONSIVE HANDLER\r\n\r\n-----------------------------------------------------------*/\r\n/*-------------- Large Screens for Desktop --------------*/\n@media (min-width: 1200px){\n.main-sidebar{\r\n    width:18%;\r\n    float: left;\n}\n.sidebar-collapse{\r\n    display: block;\n}\n.sidebar-menu .header span{\r\n    display: block;\n}\n.content-holder{\r\n    width: 79%;\r\n    margin: 20px 1% 0 1%;\r\n    float: left;\n}\r\n  /*  Change with Menu Toggled */\n.main-sidebar.hidden{\r\n    margin-left: -14%;\n}\n.content-holder.hidden{\r\n    width: 94%;\r\n    margin: 20px 1% 0 1%;\r\n    float: left;\n}\n}\r\n\r\n/*-------------- Medium Screen for Tablets  --------------*/\n@media screen (min-width: 992px), screen and (max-width: 1199px){\n.main-sidebar{\r\n    width: 23%;\r\n    float: left;\n}\n.content-holder{\r\n    width: 71%;\r\n    margin: 20px 2% 0 2%;\r\n    float: left;\n}\n.main-sidebar.active{\r\n    padding-left:15%;\n}\n.sidebar-collapse{\r\n    display: block;\n}\n.sidebar-menu .header span{\r\n    display: block;\n}\r\n\r\n  /*  Change with Menu Toggled */\n.main-sidebar.hidden{\r\n    margin-left: -18%;\n}\n.content-holder.hidden{\r\n    width: 92%;\r\n    margin: 20px 2% 0 2%;\r\n    float: left;\n}\n}\r\n/*-------------- Small Screen for Mobile Phones  --------------*/\n@media screen (min-width: 768px), screen and (max-width: 991px){\n.main-sidebar{\r\n    width: 90%;\r\n    position: absolute;\r\n    top:0;\r\n    left: 0;\r\n    z-index: 10;\r\n    background: #fff;\n}\n.content-holder{\r\n    width: 96%;\r\n    margin: 20px 2% 0 2%;\n}\n.sm-title{\r\n    text-align: center;\n}\n.sidebar-collapse{\r\n    display: none;\n}\n.sidebar-menu .header span{\r\n    display: none;\n}\n.main-sidebar.hidden{\r\n     margin-left: 0;\n}\n#toggleIcon{\r\n    display: none;\n}\n.force-collapse{\r\n    display: none;\n}\n}\r\n\r\n/*-------------- Extra Small Screen for Mobile Phones --------------*/\n@media (max-width: 767px){\n.main-sidebar{\r\n    width: 100%;\r\n    position: absolute;\r\n    top:0;\r\n    left: 0;\r\n    z-index: 10;\r\n    background-color: rgba(0,0,0,0.5);\r\n    margin-top: 100px;\n}\n.content-holder{\r\n    width: 10px;\r\n    min-width: 96%;\r\n    overflow-y: hidden;\r\n    margin: 20px 2% 0 2%;\r\n    float: left;\n}\n.main-sidebar ul{\r\n    background: #fff;\r\n    width: 90%;\r\n    min-height: 84.5vh;\n}\n.sm-title{\r\n    text-align: center;\n}\n.sidebar-menu .header span{\r\n    display: none;\n}\n.main-sidebar.hidden{\r\n    margin-left: 0;\n}\n.header .input-group{\r\n    width: 90%;\r\n    margin: 0 5% 0 5%;\n}\n#toggleIcon{\r\n    display: none;\n}\n.force-collapse{\r\n    display: none;\n}\n}\n@media (max-width: 239px){\n.main-sidebar{\r\n    width: 90%;\r\n    position: absolute;\r\n    top:0;\r\n    left: 0;\r\n    z-index: 10;\r\n    background: #fff;\r\n    margin-top: 100px;\n}\n.content-holder{\r\n    width: 96%;\r\n    margin: 20px 2% 0 2%;\r\n    float: left;\n}\n.sm-title,.header{\r\n    display: none;\n}\n.sidebar-menu  li > a{\r\n  padding: 15px 20px 15px 15px;\r\n  display: block;\n}\n.sidebar-menu  ul li > a{\r\n  padding: 10px 15px 10px 25px;\r\n  display: block;\n}\n.sidebar-menu .header span{\r\n    display: none;\n}\n.main-sidebar.hidden{\r\n    margin-left: 0;\n}\n.header .input-group{\r\n    width: 90%;\r\n    margin: 0 5% 0 5%;\n}\n#toggleIcon{\r\n    display: none;\n}\n.force-collapse{\r\n    display: none;\n}\n}\r\n"],"sourceRoot":""}]);

